import { directive as ClickOutside } from 'v-click-outside';
import excludeClickOutsideClasses from '@nextcloud/vue/dist/Mixins/excludeClickOutsideClasses';
export default {
  name: 'HeaderMenu',
  directives: {
    ClickOutside
  },
  mixins: [excludeClickOutsideClasses],
  props: {
    id: {
      type: String,
      required: true
    },
    ariaLabel: {
      type: String,
      default: ''
    },
    open: {
      type: Boolean,
      default: false
    }
  },

  data() {
    return {
      opened: this.open,
      clickOutsideConfig: {
        handler: this.closeMenu,
        middleware: this.clickOutsideMiddleware
      }
    };
  },

  watch: {
    open(newVal) {
      this.opened = newVal;
      this.$nextTick(() => {
        if (this.opened) {
          this.openMenu();
        } else {
          this.closeMenu();
        }
      });
    }

  },

  mounted() {
    document.addEventListener('keydown', this.onKeyDown);
  },

  beforeDestroy() {
    document.removeEventListener('keydown', this.onKeyDown);
  },

  methods: {
    /**
     * Toggle the current menu open state
     */
    toggleMenu() {
      // Toggling current state
      if (!this.opened) {
        this.openMenu();
      } else {
        this.closeMenu();
      }
    },

    /**
     * Close the current menu
     */
    closeMenu() {
      if (!this.opened) {
        return;
      }

      this.opened = false;
      this.$emit('close');
      this.$emit('update:open', false);
    },

    /**
     * Open the current menu
     */
    openMenu() {
      if (this.opened) {
        return;
      }

      this.opened = true;
      this.$emit('open');
      this.$emit('update:open', true);
    },

    onKeyDown(event) {
      // If opened and escape pressed, close
      if (event.key === 'Escape' && this.opened) {
        event.preventDefault();
        /** user cancelled the menu by pressing escape */

        this.$emit('cancel');
        /** we do NOT fire a close event to differentiate cancel and close */

        this.opened = false;
        this.$emit('update:open', false);
      }
    }

  }
};